import { NodePool, instantiate } from 'cc';
import { Prefab } from 'cc';
import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

export interface PoolParams {
    name?: string, //
    initCount?: number, //
    prefab?: Prefab | Node, //
    mediator?: string, //
    cb?: Function, //
    pool?: NodePool, //
    nodeName?: string, //
}

const PoolsName = {
    element: 'element',
    giftEffect: 'giftEffect'
}

@ccclass('UnicornPoolManager')
export class UnicornPoolManager extends Component {
    private static _instance: UnicornPoolManager = null;

    public static get instance() {
        if (!this._instance) {
            this._instance = new UnicornPoolManager();
        }

        return this._instance;
    }

    /**
     * 
     */
    private pools: { [key: string]: PoolParams } = {};

    /**
     * 
     */
    public static PoolsName = PoolsName;

    /**
     * 
     * @param  {[PoolParams]} params [{
     *      name:
     *      initCount:
     *      mediator
     *      prefab:
     * 
     * }]
     */
    createPool(params: PoolParams) {
        // return new Promise<void>((resolve, reject) => {
        // console.log(params.prefab, "prefab");
        if (this.pools[params.name]) return //reject();
        // console.log("createPool", params.name, "start");
        const mediator = params.mediator || "";
        const pool = new NodePool(mediator);
        const initCount = params.initCount || 1;
        const nodeName = params.nodeName ? params.nodeName : params.prefab.name;
        for (let i = 0; i < initCount; ++i) {
            let node = instantiate(params.prefab) as Node; // 
            node.name = '[' + params.name + ']' + nodeName;
            pool.put(node); //  putInPool 
        }
        params.pool = pool;
        this.pools[params['name']] = params;
        //     resolve();
        // });
    }

    /**
    * 
    * @param  {[PoolParams]} params [{
    *      name:
    *      initCount:
    *      mediator
    *      prefab:
    * 
    * }]
    */
    createPoolAsync(params: PoolParams) {
        // console.log(params.prefab, "prefab")
        if (this.pools[params.name]) {
            if (params.cb) params.cb();
            return;
        }
        console.log("createPool", "start")
        const mediator = params.mediator || "";
        const pool = new NodePool(mediator);
        const initCount = params.initCount || 1;
        let num = 0;
        const nodeName = params.name ? params.name : params.prefab.name;
        for (let i = 0; i < initCount; ++i) {
            setTimeout(function () {
                let node = instantiate(params.prefab) as Node; // 
                node.name = '[' + params.name + ']' + nodeName;
                pool.put(node); //  putInPool 
                num++;
                if (num >= initCount && params.cb) {
                    return params.cb();
                }
            }, 0);
        }
        params.pool = pool;
        this.pools[params.name] = params;
        console.log("createPool", "done")
    }

    /**
     * 
     * @param name 
     */
    clearPool(name: string) {
        console.log("clearPool", name)
        console.log(this.pools[name], ":" + name)
        this.pools[name].pool.clear();
        delete this.pools[name];
    }

    /**
     * 
     * @param name 
     */
    clearPoolContent(name: string) {
        console.log("clearPoolContent", name)
        console.log(this.pools[name], ":" + name)
        this.pools[name].pool.clear();
    }

    /**
     * 
     * @param name 
     * @returns NodePool
     */
    getPool(name: string) {
        console.log(this.pools[name], ":" + name)
        return this.pools[name].pool;
    }

    /**
     * 
     * @param  {[type]} name []
     * @param  {[type]} params []
     * @return {[type]}  []
     */
    getPoolObj(name: string, params?: PoolParams) {
        // console.log(this.pools[name], ":" + name);
        if (this.pools[name].pool.size() == 0) { //  size 
            // console.log(name, '');
            const obj = instantiate(this.pools[name].prefab) as Node;
            this.pools[name].pool.put(obj);
        }
        return this.pools[name].pool.get(params);//mediatorreuse
    }

    /**
     * 
     * @param name 
     * @returns {[PoolParams]} PoolParmas
     */
    getPoolParams(name: string) {
        // console.log(this.pools[name], ":" + name);
        return this.pools[name];
    }

    /**
     * 
     * @param  {[type]} name []
     * @param  {[type]} obj  []
     */
    putPoolObj(name: string, obj: Node) {
        // console.log(this.pools[name], ":" + name)
        this.pools[name].pool.put(obj);
    }
}


